      <!-- 수정버튼 -->
      <div class="modify_pop popup" v-show="modifyIs">
        <div>
          <h3>차량 정보 수정</h3>
          <button @click="modifyClose">
            <font-awesome-icon icon="times-circle" />
          </button>
          <div class="form_window">
            <form>
              <p>차량번호<span>(필수)</span></p>
              <input
                type="text"
                placeholder="이곳에 차량번호를 입력해주세요."
                required
                v-model="current.lp"
              />
              <p>개시시점<span></span></p>
              <input type="datetime-local" v-model="current.startTime" />
              <p>종료시점<span></span></p>
              <input type="datetime-local" v-model="current.endTime" />
              <p>이름<span></span></p>
              <input type="text" v-model="current.name" />
              <p>연락처</p>
              <input type="text" v-model="current.phone" />
              <p>입주사ID</p>
              <input type="text" v-model="current.resident_id" readonly />
              <p>비고</p>
              <input type="text" v-model="current.contents" />
              <button @click.prevent="modifyVehicle" class="modifybtn">
                수정
              </button>
              <button @click.prevent="deleteVehicle" class="deletebtn">
                삭제
              </button>
            </form>
          </div>
        </div>
      </div>
      <div class="list-items">
        <div class="list-title">
          <li><span>번호 </span></li>
          <li>
            <span
              >차량번호<button @click.prevent="item_lp_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.lp_click"
                /><font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li>
            <span
              >개시시점<button @click.prevent="item_startTime_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.startTime_click"
                /><font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li>
            <span
              >종료시점<button @click.prevent="item_endTime_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.endTime_click"
                /><font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li>
            <span
              >이름<button @click.prevent="item_name_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.name_click"
                /><font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li>
            <span
              >연락처<button @click.prevent="item_phone_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.phone_click"
                /><font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li>
            <span>입주사 ID</span>
          </li>
          <li>
            <span
              >메모<button @click.prevent="item_contents_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.contents_click"
                /><font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
        </div>
        <!-- 리스트 출력부분 -->
        <!-- <spinners v-if="isLoading" /> -->
        <ul class="list-wrap">
          <li
            v-for="(ad, index) in resistRocord"
            :key="index"
            @click="modifyOpen(ad)"
          >
            <span> {{ index + 1 }}</span>
            <span class="lp"
              ><input type="text" v-model="ad.lp" readonly
            /></span>
            <span class="startTime"
              ><input type="text" v-model="ad.startTime" readonly
            /></span>
            <span class="endTime"
              ><input type="text" v-model="ad.endTime" readonly
            /></span>
            <span class="name"
              ><input type="text" v-model="ad.name" readonly
            /></span>
            <span class="phone"
              ><input type="text" v-model="ad.phone" readonly
            /></span>
            <span class="id"
              ><input type="text" v-model="ad.resident_id" readonly
            /></span>
            <span class="contents"
              ><input type="text" v-model="ad.contents" readonly
            /></span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "underscore"
import XLSX from "xlsx"
import config from "../config.js"
// import spinners from "../components/spinners"
export default {
  data: function() {
    return {
      isLoading: false,
      searchLp: "",
      id: "",
      resistRocord: [],
      admin: [
        {
          order: "",
          lp: "",
          startTime: "",
          endTime: "",
          name: "",
          phone: "",
          resident_id: "",
          contents: "",
          number: "",
          check: true
        }
      ],
      current: {
        lp: "",
        startTime: "",
        endTime: "",
        name: "",
        phone: "",
        contents: "",
        number: "",
        resident_id: ""
      },
      sort_item: {
        lp_click: false,
        startTime_click: false,
        endTime_click: false,
        name_click: false,
        phone_click: false,
        contents_click: false
      },
      reg: {
        lp: "",
        startTime: format_time1(new Date(Date.now())),
        endTime: format_time1(new Date(Date.now())),
        name: "",
        phone: "",
        resident_id: "",
        contents: "",
        number: ""
      },
      check: true,
      resistIs: false,
      modifyIs: false
    }
  },
  created() {
    // this.isLoading = true
    this.$socket.on("message_from_server", data => {
      console.log("data.kind : ", data.kind)
      // this.isLoading = false
      this.sales_list(data)
    }),
      this.search1()
  },
  components: {
    // spinners
  },
  methods: {
    registOpen() {
      this.admin.lp = ""
      this.admin.startTime = ""
      this.admin.endTime = ""
      this.admin.name = ""
      this.admin.phone = ""
      this.admin.resident_id = ""
      this.admin.contents = ""
      this.admin.number = ""
      this.resistIs = true
    },
    registClose() {
      this.resistIs = false
    },
    registNew(e) {
      this.registClose()
      e.preventDefault()
      console.log("this.admin : ", this.admin)
      let obj = {}
      obj.lp = this.reg.lp
      obj.period_start = get_datetime(this.reg.startTime) 
      obj.period_end = get_datetime(this.reg.endTime)
      obj.name = this.reg.name
      obj.phone1 = this.reg.phone
      obj.id = localStorage.getItem("id")
      obj.contents = this.reg.contents
      obj.number = obj.lp.replace(/[가-힝]/gi, "")
      obj.kind = "create_visited_reserve_vehicle_list"
      console.log("obj :", obj)
      this.sendMessage(obj)
    },
    item_lp_click: function() {
      console.log("lp sort")
      let temp10 = []
      if (this.sort_item.lp_click === false) {
        console.log("lp sort1")
        this.sort_item.lp_click = true
        temp10 = _.sortBy(this.resistRocord, "lp")
        temp10.reverse()
        return (this.resistRocord = temp10)
      } else {
        console.log("lp sort2")
        this.sort_item.lp_click = false
        temp10 = _.sortBy(this.resistRocord, "lp")
        return (this.resistRocord = temp10)
      }
    },
    item_startTime_click: function() {
      console.log("startTime sort")
      let temp10 = []
      if (this.sort_item.startTime_click === false) {
        console.log("startTime sort1")
        this.sort_item.startTime_click = true
        temp10 = _.sortBy(this.resistRocord, "startTime")
        temp10.reverse()
        return (this.resistRocord = temp10)
      } else {
        console.log("startTime sort2")
        this.sort_item.startTime_click = false
        temp10 = _.sortBy(this.resistRocord, "startTime")
        return (this.resistRocord = temp10)
      }
    },
    item_endTime_click: function() {
      console.log("endTime sort")
      let temp10 = []
      if (this.sort_item.endTime_click === false) {
        console.log("endTime sort1")
        this.sort_item.endTime_click = true
        temp10 = _.sortBy(this.resistRocord, "endTime")
        temp10.reverse()
        return (this.resistRocord = temp10)
      } else {
        console.log("endTime sort2")
        this.sort_item.endTime_click = false
        temp10 = _.sortBy(this.resistRocord, "endTime")
        return (this.resistRocord = temp10)
      }
    },
    item_name_click: function() {
      console.log("name sort")
      let temp10 = []
      if (this.sort_item.name_click === false) {
        console.log("name sort1")
        this.sort_item.name_click = true
        temp10 = _.sortBy(this.resistRocord, "name")
        temp10.reverse()
        return (this.resistRocord = temp10)
      } else {
        console.log("name sort2")
        this.sort_item.name_click = false
        temp10 = _.sortBy(this.resistRocord, "name")
        return (this.resistRocord = temp10)
      }
    },
    item_phone_click: function() {
      console.log("phone sort")
      let temp10 = []
      if (this.sort_item.phone_click === false) {
        console.log("phone sort1")
        this.sort_item.phone_click = true
        temp10 = _.sortBy(this.resistRocord, "phone")
        temp10.reverse()
        return (this.resistRocord = temp10)
      } else {
        console.log("phone sort2")
        this.sort_item.phone_click = false
        temp10 = _.sortBy(this.resistRocord, "phone")
        return (this.resistRocord = temp10)
      }
    },
    item_contents_click: function() {
      console.log("contents sort")
      let temp10 = []
      if (this.sort_item.contents_click === false) {
        console.log("contents sort1")
        this.sort_item.contents_click = true
        temp10 = _.sortBy(this.resistRocord, "contents")
        temp10.reverse()
        return (this.resistRocord = temp10)
      } else {
        console.log("contents sort2")
        this.sort_item.contents_click = false
        temp10 = _.sortBy(this.resistRocord, "contents")
        return (this.resistRocord = temp10)
      }
    },
    search() {
      console.log("검색 버튼을 눌렀습니다.", this.searchLp)
      if (this.searchLp !== "") {
        let temp_resistRocord = []
        for (let i = 0; i < this.resistRocord.length; i++) {
          if (this.resistRocord[i].lp.indexOf(this.searchLp) !== -1) {
            temp_resistRocord.push(this.resistRocord[i])
          }
        }
        this.resistRocord = temp_resistRocord
      } else {
        this.search1()
      }
    },
    search1() {
      let obj = {}
      obj.kind = "get_visited_reserve_vehicle_list"
      this.sendMessage(obj)
    },
    sales_list(data) {
      if (data.kind === "get_visited_reserve_vehicle_list") {
        console.log("visited_reserve_vehicle_list : ", data)   //(format_time1(new Date(sales_list[i].period_end)).toString()).substring(0,11)
        var last_time = {}
        var visited_reserve_vehicle_list = []
        this.resistRocord = []
        if (data.docs && data.docs.length > 0) {
          if (data.docs && data.docs.length === 1) {
            visited_reserve_vehicle_list.push(data.docs[0])
          } else {
            for (let k = 0; k < data.docs.length; k++) {
              if (data.docs[k].mark === true) {
                continue
              }
              last_time = data.docs[k]
              for (let l = k + 1; l < data.docs.length; l++) {
                if (data.docs[k].lp === data.docs[l].lp) {
                  if (last_time.time < data.docs[l].time) {
                    last_time = data.docs[l]
                  }
                  data.docs[l].mark = true
                }
              }
              visited_reserve_vehicle_list.push(last_time)
            }
          }
          for (var i = 0; i < visited_reserve_vehicle_list.length; i++) {
            var obj = {}
            obj.lp = visited_reserve_vehicle_list[i].lp
            obj.name = " "
            if (visited_reserve_vehicle_list[i].name) {
              obj.name = visited_reserve_vehicle_list[i].name
            }
            obj.phone = " "
            if (visited_reserve_vehicle_list[i].phone) {
              obj.phone = visited_reserve_vehicle_list[i].phone
            }
            obj.startTime = format_time1(new Date(0))
            if (visited_reserve_vehicle_list[i].period_start) {
              obj.startTime = (format_time1( 
                new Date(visited_reserve_vehicle_list[i].period_start)     //(format_time1(new Date(sales_list[i].period_end)).toString()).substring(0,11)
              ).toString()).substring(0,17)
              obj.period_start = visited_reserve_vehicle_list[i].period_start
            }
            obj.endTime = format_time1(new Date(0))
            if (visited_reserve_vehicle_list[i].period_end) {
              obj.endTime = (format_time1(
                new Date(visited_reserve_vehicle_list[i].period_end)
              ).toString()).substring(0,17)
              obj.period_end = visited_reserve_vehicle_list[i].period_end
            }
            obj.number = " "
            if (visited_reserve_vehicle_list[i].number) {
              obj.number = visited_reserve_vehicle_list[i].number
            }
            obj.resident_id = " "
            if (visited_reserve_vehicle_list[i].id) {
              obj.resident_id = localStorage.getItem("id")
            }
            obj.contents = " "
            if (visited_reserve_vehicle_list[i].contents) {
              obj.contents = visited_reserve_vehicle_list[i].contents
            }

            this.resistRocord.push(obj)
          }

          console.log("this.resistRocord : ", this.resistRocord)
        }
      } else if (
        data.kind === "create_visited_reserve_vehicle_list" ||
        data.kind === "update_visited_reserve_vehicle_list" ||
        data.kind === "delete_visited_reserve_vehicle_list"
      ) {
        this.search1()
      }
    },
    modifyOpen(c) {
      this.modifyIs = true
      this.current.lp = c.lp
      this.current.name = c.name
      this.current.phone = c.phone
      this.current.startTime = c.startTime
      this.current.endTime = c.endTime
      this.current.resident_id = localStorage.getItem("id")
      this.current.contents = c.contents
      this.$refs.btn.focus()
      console.log("c : ", c.lp)
      console.log("this : ", this)
    },
    modifyClose() {
      this.modifyIs = false
    },
    searchItems() {
      console.log("테스트")
    },
    sendMessage(c) {
      this.$socket.emit("message_from_web_client", c)
    },
    modifyVehicle() {
      console.log("차량정보 수정")
      this.modifyClose()
      let obj = {}
      obj.lp = this.current.lp
      obj.name = this.current.name
      obj.phone1 = this.current.phone
      obj.period_start = get_datetime(this.current.startTime)
      obj.period_end = get_datetime(this.current.endTime)
      obj.contents = this.current.contents
      obj.id = localStorage.getItem("id")
      obj.number = obj.lp.replace(/[가-힝]/gi, "")
      obj.kind = "update_visited_reserve_vehicle_list"
      console.log("차량정보수정 :", obj)
      this.sendMessage(obj)
    },
    deleteVehicle() {
      console.log("차량정보 삭제")
      this.modifyClose()
      let obj = {}

      obj.lp = this.current.lp
      obj.name = this.current.name
      obj.phone1 = this.current.phone
      obj.period_start = this.current.period_start
      obj.period_end = this.current.eperiod_end
      obj.contents = this.current.contents
      obj.id = localStorage.getItem("id")
      obj.number = obj.lp.replace(/[가-힝]/gi, "")
      obj.kind = "delete_visited_reserve_vehicle_list"
      this.sendMessage(obj)
    },
    download: function() {
      let dt = new Date()
      let year = itoStr(dt.getFullYear())
      let month = itoStr(dt.getMonth() + 1)
      let day = itoStr(dt.getDate())
      let hour = itoStr(dt.getHours())
      let mins = itoStr(dt.getMinutes())
      let postfix = year + "_" + month + "_" + day + "_" + hour + "_" + mins
      const data = XLSX.utils.json_to_sheet(this.resistRocord)
      const wb = XLSX.utils.book_new()
      XLSX.utils.book_append_sheet(wb, data, "data")
      XLSX.writeFile(
        wb,
        config.parking_name + "_방문예약_차량관리_" + postfix + ".xlsx"
      )
    }
  }
}

function format_time1(dat) {
  var str = ""
  str += dat.getFullYear()
  str += "." + num_to_str(dat.getMonth() + 1)
  str += "." + num_to_str(dat.getDate())
  str += ". " + num_to_str(dat.getHours())
  str += ":" + num_to_str(dat.getMinutes())
  str += ":" + num_to_str(dat.getSeconds())
  return str
}

function num_to_str($num) {
  $num < 10 ? ($num = "0" + $num) : $num
  return $num.toString()
}

function get_datetime(str) {
  var res = str.split(/[-T.:\s]+/)
  var d = new Date(
    parseInt(res[0]),
    parseInt(res[1]) - 1,
    parseInt(res[2]),
    parseInt(res[3]),
    parseInt(res[4]),
    0,
    0
  )
  return d.getTime()
}
function itoStr($num) {
  $num < 10 ? ($num = "0" + $num) : $num
  return $num.toString()
}
</script>

<style scoped>
@import "../assets/css/table.css";
@import "../assets/css/divice.css";
</style>
